html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  color: #F9C802;
  background: #000; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  background: #1a213e;
  color: #fff;
  font-family: 'Open Sans'; }

.bear {
  margin-bottom: 30px; }

.container {
  max-width: 1170px;
  padding: 30px;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
  width: 100%;
  z-index: 2; }
  .container--flex {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: flex-start; }
    @media (max-width: 768px) {
      .container--flex {
        display: block;
        padding: 30px 10px; } }

.st0 {
  stroke-width: 1px;
  stroke: #fff;
  fill: rgba(0, 0, 0, 0);
  transition: fill 0.3s ease-in; }

.main {
  min-height: 100vh;
  background: url("/assets/img/bg.jpg");
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  flex-direction: column;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  padding: 30px; }
  @media (max-width: 768px) {
    .main {
      background-attachment: initial; } }
  .main:after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(26, 33, 62, 0.9);
    content: ""; }

h1 {
  position: relative;
  z-index: 2;
  margin-bottom: 30px; }

.logo {
  width: 300px;
  flex-shrink: 0;
  position: relative;
  z-index: 2;
  margin: 0 auto; }

.form {
  position: relative;
  z-index: 2;
  width: 300px; }
  .form label {
    display: block;
    width: 100%;
    cursor: pointer;
    font-size: 14px; }
  .form input {
    display: block;
    width: 100%;
    margin-top: 5px;
    margin-bottom: 15px;
    padding: 10px;
    background: rgba(255, 255, 255, 0.7);
    border: 1px solid transparent;
    outline: none;
    border-radius: 3px;
    font-size: 18px; }
  .form .invalid {
    border: 1px solid #E61E73; }
  .form__send {
    width: 100%;
    padding: 15px;
    background: #E61E73;
    border: none;
    color: #fff;
    font-size: 14px;
    margin-top: 15px;
    border-radius: 3px;
    cursor: pointer; }
  .form--disabled {
    display: none; }
  .form--enabled {
    display: block; }

.message {
  position: relative;
  z-index: 2;
  width: 300px; }
  .message--disabled {
    display: none; }
  .message textarea {
    display: block;
    width: 300px;
    margin-top: 5px;
    margin-bottom: 15px;
    padding: 10px;
    background: rgba(255, 255, 255, 0.7);
    border: none;
    outline: none;
    border-radius: 3px;
    font-size: 18px; }

.marker {
  display: block;
  max-width: 300px; }
  .marker__wrapper {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center; }

.rotate {
  display: none; }

@media (orientation: portrait) {
  .rotate {
    position: fixed;
    width: 100%;
    height: 100vh;
    background: #1a213e;
    z-index: 2000;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column; }
    .rotate img {
      display: block;
      margin-bottom: 30px;
      max-width: 200px; } }

.facebook {
  position: fixed;
  width: 100%;
  height: 100vh;
  background: #1a213e;
  z-index: 3000;
  top: 0;
  left: 0;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  display: none; }
  .facebook--visible {
    display: flex; }
